html {
  font-family: "Montserrat", sans-serif;
  font-size: 16px;
  line-height: 1.6;
  color: #595959; }

/* Link Styles
======================= */
/* Paragraph Styles
======================= */
p {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

/* Base Image Styles 
======================= */
img {
  width: 100%; }

pre {
  display: block;
  margin: 3px 0;
  padding: 10px;
  color: #D4D4A5;
  border-radius: 5px;
  background-color: #333333;
  transition: box-shadow 300ms cubic-bezier(0, 0, 0.03, 1);
  box-shadow: 0 0 0 0 rgba(0, 0, 0, 0.03), 0 0 0 0 rgba(0, 0, 0, 0.03), 0 0 0 0 rgba(0, 0, 0, 0.02); }
  pre:hover {
    transition: box-shadow 300ms cubic-bezier(0, 0, 0.03, 1);
    box-shadow: 0 12px 16px 2px rgba(0, 0, 0, 0.03), 0 5px 22px 4px rgba(0, 0, 0, 0.03), 0 6px 13px 0 rgba(0, 0, 0, 0.02); }

code {
  margin: 0 5px;
  padding: 1px 5px;
  background-color: #d9d9d9; }

.wsu-u {
  /* Design System Inline Documentation
	======================= */
  /** Padding */
  /* Alignment
	======================= */ }
  .wsu-u-visually-hidden {
    position: absolute;
    overflow: hidden;
    clip: rect(1px, 1px, 1px, 1px);
    width: 1px;
    height: 1px;
    margin: -1px;
    padding: 0;
    clip-path: inset(50%); }
  .wsu-u-spacer {
    display: flex;
    align-items: center;
    flex-flow: column wrap;
    justify-content: space-between;
    height: 200px;
    margin: 20px 0;
    padding: 20px 40px;
    background: linear-gradient(-195deg, #fff, #e6e6e6); }
    @media only screen and (min-width: 768px) {
      .wsu-u-spacer {
        flex-flow: row nowrap; } }
  .wsu-u-title {
    font-family: monospace;
    display: flex;
    align-items: center;
    justify-content: center; }
  .wsu-u-toc {
    display: block;
    max-width: 400px;
    margin: 15px auto;
    padding: 10px 20px;
    color: #fff;
    background: linear-gradient(-195deg, gray, #4d4d4d);
    font-size: 14px;
    font-weight: 300;
    line-height: 1.5;
    transition: box-shadow 300ms cubic-bezier(0, 0, 0.03, 1);
    box-shadow: 0 8px 10px 1px rgba(0, 0, 0, 0.03), 0 3px 14px 2px rgba(0, 0, 0, 0.03), 0 4px 15px 0 rgba(0, 0, 0, 0.02); }
    .wsu-u-toc p {
      margin: 0; }
    .wsu-u-toc a {
      transition: color 300ms cubic-bezier(0, 0, 0.03, 1);
      color: #fff; }
      .wsu-u-toc a:hover {
        color: #cccccc; }
  .wsu-u-requirements {
    width: 400px; }
  .wsu-u-pad--none {
    padding-top: 0;
    padding-right: 0;
    padding-bottom: 0;
    padding-left: 0; }
  .wsu-u-pad--less {
    padding-top: 0;
    padding-right: 0;
    padding-bottom: 0;
    padding-left: 0; }
  .wsu-u-pad--more {
    padding-top: 0;
    padding-right: 0;
    padding-bottom: 0;
    padding-left: 0; }
  .wsu-u-pad-bottom--none {
    padding-bottom: 0; }
  .wsu-u-pad-top--none {
    padding-top: 0; }
  .wsu-u-pad-left--none {
    padding-left: 0; }
  .wsu-u-pad-right--none {
    padding-right: 0; }
  .wsu-u-textalign--left {
    text-align: left; }
  .wsu-u-textalign--right {
    text-align: right; }
  .wsu-u-textalign--center {
    text-align: center; }

.wsu-u-background--gray-0 {
  background-color: #f2f2f2; }

.wsu-u-background--gray-5 {
  background-color: #f2f2f2; }

.wsu-u-background--gray-10 {
  background-color: #e6e6e6; }

.wsu-u-background--gray-15 {
  background-color: #d9d9d9; }

.wsu-u-background--gray-20 {
  background-color: #cccccc; }

.wsu-u-background--gray-25 {
  background-color: #bfbfbf; }

.wsuwp-explore-control__option-set {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: auto;
  height: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  display: none; }
  .wsuwp-explore-control__option-set.active {
    display: flex; }
  .wsuwp-explore-control__option-set__options {
    display: flex;
    margin: 0 !important;
    padding: 2rem !important;
    list-style-type: none;
    background-color: #f2f2f2; }
  .wsuwp-explore-control__option-set__option {
    display: flex;
    margin: 0 !important;
    width: 150px;
    padding: 0.5rem;
    box-sizing: border-box;
    min-height: 125px;
    justify-content: center;
    align-items: center;
    background-color: #fff;
    border-left: 1px solid #f2f2f2;
    cursor: pointer; }
    .wsuwp-explore-control__option-set__option:hover {
      border: 1px solid #A60F2D; }
